.container {
  max-width: 580px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.board {
  display: flex;
  flex-direction: row;
  max-width: 580px;
  width: 100%;
}

.list {
  width: 100%;
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  height: 245px;
  flex-direction: column;
}

.column {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.item {
  text-align: left;
}

.completed {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.total {
  text-shadow: 0px 0px 16px rgba(51, 51, 255, 0.25),
    0px 0px 8px rgba(51, 51, 255, 0.25), 0px 4px 32px rgba(51, 51, 255, 0.5);
}

.done {
  color: #00cccc;
}
